module.exports = "<div id=create-mc-question> <mdl-button primary raised class=float-button style=\"bottom: 80px\" @click=\"questionInbox.show = true\" v-bind:disabled=publishButton.disabled> <i class=material-icons>inbox</i> {{questionInbox.questions.length}} </mdl-button> <mdl-button primary raised class=float-button @click=publishQuestion() v-bind:disabled=publishButton.disabled> 發佈 </mdl-button> <div class=body-wrapper> <card> <div slot=content style=\"padding: 16px 16px 0 16px\"> <div class=flex-column> <div class=flex-row> <div class=\"flex-column flex-50\"> <span class=field-title>科目</span> <select v-model=newQuestion.subject> <option v-for=\"subject in subjects\" v-bind:value=subject.id> {{ subject.name }} </option> </select> </div> <div class=\"newquestion-difficulty-box flex-column flex-50\"> <span class=field-title style=margin-bottom:4px>難度</span> <span class=flex-row> <i v-for=\"1 in 5\" class=material-icons @click=\"newQuestion.difficulty = $index + 1\" :class=\"{'difficulty-heighlight': newQuestion.difficulty > $index}\">star_rate</i> </span> </div> </div> <div class=flex-column> <div style=\"padding-top: 25px;margin-right: 10px\"> <span class=field-title>標籤</span> </div> <div class=\"flex-row flex-center flex-wrap\"> <span class=q-tag @click=removeTag($index) v-for=\"tag in newQuestion.tags\" track-by=$index>{{tag}}</span> </div> <div style=\"position: relative;top: -12px\"> <mdl-textfield label=輸入標籤.回車 @keyup.enter=addTag() :value.sync=tag style=width:200px></mdl-textfield> </div> </div> </div> </div> </card> <h5 style=margin-left:4px>編輯題目</h5> <div class=flex-row> <div class=flex-50> <card> <div slot=content> <quill :content.sync=editorPreview.question></quill> </div> </card> </div> <div class=flex-50> <card> <div slot=content> <p class=toolbar-title>預覽</p> <div id=question-preview-container></div> </div> </card> </div> </div> <div class=mc-preview-wrapper id=mc-preview-container> <p style=margin:0;text-align:center;color:#9E9E9E>點擊選項設定正確答案</p> <di